

.Loading-screen{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgb(10, 65, 22);
  /*  transition: opacity 0.75s, visibility 0.75s;*/

}




.Loading{
    width: 100%;
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: opacity 0.75s, visibility 0.75s;
    position: absolute;
    top: 90px;
}


img{
 margin-top:-13%;
}



.Loading .line-box{
    padding: 2px;
    width: 40%;
    height: 20px;
    border: 2px solid rgb(0, 0, 0);
    border-radius: 20px;
}

.Loading .line-box .line{
    height: 20px;
    border-radius: 20px;
    background-color: rgb(255, 255, 255);
    animation: Loading 2s forwards ;
   
}


.loader--hidden{
    transition: 2.5s;
     opacity: 0; 
   visibility: hidden;
    }



@keyframes Loading {
    0%{
         width: 0%;
     }
 
     23%{
         width: 17%;
     }
     54%{
         width: 46%;
     }
 
     74%{
         width: 65%;
     }
 
     100%{
         width: 100%;
     }
 }



