.Loading-screen {
  background: #0a4116;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.Loading {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  transition: opacity .75s, visibility .75s;
  display: flex;
  position: absolute;
  top: 90px;
}

img {
  margin-top: -13%;
}

.Loading .line-box {
  border: 2px solid #000;
  border-radius: 20px;
  width: 40%;
  height: 20px;
  padding: 2px;
}

.Loading .line-box .line {
  background-color: #fff;
  border-radius: 20px;
  height: 20px;
  animation: 2s forwards Loading;
}

.loader--hidden {
  opacity: 0;
  visibility: hidden;
  transition: all 2.5s;
}

@keyframes Loading {
  0% {
    width: 0%;
  }

  23% {
    width: 17%;
  }

  54% {
    width: 46%;
  }

  74% {
    width: 65%;
  }

  100% {
    width: 100%;
  }
}

